@font-face {
    font-family: CloisterBlack;
    src: url(../../font/CloisterBlack.ttf);
}

.blogContentBox {
    padding: 0px 100px 0px 55px;
    background-color: rgb(49, 51, 56);
    background-image: linear-gradient(rgb(49, 51, 70), rgb(29, 29, 30));
}

.blogContentArea {
    padding: 30px 0px 30px 35px;
    border-left: 25px solid white;

}

.articleElementBox {
    height: 450px;
    background-color: white;
    border-radius: 20px;
}

.articleElementImage {
    height: 330px; 
    background-color: gray;
    border-radius: 20px 20px 0px 0px;
}

.blogToolbar {
    position: fixed;
    top: 0;
    height: 75px;
    width: 100%;

    display: flex; 
    flex-direction: row; 
    justify-content: space-between; 
    align-items: center;
}

.blogToolbarTitle {
    font-family: CloisterBlack;
    font-size: 56px;
    color: white;
    padding-left: 50px;
}


